@import url("https://fonts.googleapis.com/css?family=Noto+Sans");
@font-face {
  font-family: "EYfont";
  src: url("/fonts/EYInterstate-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'EYfont-Bold';
  src: url("/fonts/EYInterstate-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal; }

body {
  margin: 0;
  background-color: #000;
  padding: 0;
  color: #fff;
  font-family: "EYfont", "Noto Sans", Arial, sans-serif;
  font-size: 1em; }

canvas {
  display: block; }

.golden {
  color: #ffe600 !important; }

.black {
  color: #000 !important; }

.white {
  color: #fff !important; }

.bold {
  font-family: "EYfont-Bold", "EYfont", "Noto Sans", Arial, sans-serif; }



