.container {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%; }
  .container .skyline {
    background-image: url("/img/galaxy.jpg");
    background-size: cover;
    background-position: top center; }
    .container .skyline .boundary {
      width: 100vw;
      height: 100vh;
      display: flex;
      justify-content: center;
      align-items: center; }
      .container .skyline .boundary > img {
        max-width: 100%;
        max-height: 100%; }



