.container {
  color: #000;
  font-size: 22vh;
  font-family: "EYfont-Bold", "EYfont", "Noto Sans", Arial, sans-serif;
  text-align: center; }
  .container .outer, .container .inner {
    position: absolute;
    top: 0;
    left: 50%;
    padding: 1rem 0;
    transform: translateX(-50%); }
    .container .outer span, .container .inner span {
      display: block;
      text-align: left;
      text-shadow: 0px 0px 4px #f0f0f0; }
  .container .inner {
    color: #ffe600; }
    .container .inner span {
      max-width: 100%;
      overflow: hidden;
      transition: width ease-in-out 1s; }



